import { REQUEST_SUCCEEDED_CODE, } from "@/common/constants";
import { request } from "@/utils/request";
import { getMyUserInfo } from "@/common/storageFunctions";
import { getUserinfo, } from "@/common/requestFunctions";
/**
 * buguVip 布咕会员
 * @description 布咕会员开通界面
 * @Author: 穆兰
 * @Date: 2022/1/9
 * @LastEditors: 穆兰
 * @LastEditTime: 2022/1/9
 */
export default {
    data() {
        return {
            userInfo: {},
            typeSelect: 0 //选择的vip类型
        };
    },
    onLoad() {
        this.refreshUserInfo();
    },
    methods: {
        /**
         *@description 点击返回按钮
         */
        onNarLeftClick() {
            uni.navigateBack({
                delta: 1
            });
        },
        /**
         * @description 更新用户信息
         */
        async refreshUserInfo() {
            uni.showLoading({
                title: "加载中"
            });
            let userInfoBefore = await getMyUserInfo();
            this.userInfo = await getUserinfo(userInfoBefore.id);
            uni.hideLoading();
        },
        /**
         * @description 选择vip的种类
         * @param {number} select 选择的第几个
         */
        onVipTypeClick(select) {
            this.typeSelect = select;
        },
        /**
         * @description 点击开通会员的按钮
         */
        onOpenVipClick() {
            uni.showModal({
                title: '开通会员',
                content: '暂时还不能开通哦~，参加官方活动可以免费领取会员哦~'
            });
        },
        /**
         * @description 新用户领取一个月vip
         */
        async getFreeVip() {
            uni.showLoading({
                title: "领取中"
            });
            let res = await request({
                data: {
                    method: 'PUT',
                    group: 'welfare',
                    action: `vip/oneMonth`,
                    data: {},
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                uni.showToast({
                    title: '领取成功',
                    icon: 'success'
                });
                this.refreshUserInfo();
            }
        },
        /**
         *
         * @function 连续发布五条动态领取vip
         */
        async getVipByActivitySum() {
            let res = await request({
                data: {
                    method: 'PUT',
                    group: 'welfare',
                    action: `getByActivitySum`,
                    data: {},
                    header: {
                        'content-type': 'application/x-www-form-urlencoded',
                    },
                }
            });
            if (res.data.code === REQUEST_SUCCEEDED_CODE) {
                uni.showToast({
                    title: '领取成功',
                    icon: 'success'
                });
                this.refreshUserInfo();
            }
        }
    }
};
